import React from "react";
import Sdata from "./Sdata";

export default function FullWidthTable(props) {
  return (
    <>
      <div className="border-b shadow-lg ml-8 my-8" style={{ width: "40vw" }}>
        <div className="bg-white justify-center flex">
          <p className="p-4">Simple Full Width Table</p>
          <div style={{ width: "25vw" }}>
            <div className="float-right  justify-center flex mt-4 mr-4 ">
              <button
                className="border text-blue-500 rounded-l-md  text-xs"
                style={{ width: "20px" }}
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className="h-4 w-4"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={1}
                    d="M15 19l-7-7 7-7"
                  />
                </svg>
              </button>
              <button className="border px-1 text-blue-500 text-sm w-5">
                1
              </button>
              <button className="border px-1 text-blue-500 text-sm w-5">
                2
              </button>
              <button className="border px-1 text-blue-500 text-sm w-5">
                3
              </button>
              <button className="border text-blue-500 rounded-r-md w-5 text-xs">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className="h-4 w-4"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={1}
                    d="M9 5l7 7-7 7"
                  />
                </svg>
              </button>
            </div>
          </div>
        </div>
        <div className="bg-white m-4">
          <table className="table-fixed w-96">
            <thead>
              <th className="border-b p-3 font-bold w-12">{props.no}</th>
              <th className="border-b p-3 font-bold w-72">
                {props.headingone}
              </th>
              <th className="border-b p-3 font-bold w-40">{props.Kitnahua}</th>
              <th className="border-b p-3 font-bold w-20">{props.badge}</th>
            </thead>
            <tbody>
              {Sdata.map((values) => {
                return (
                  <tr key={values.index}>
                    <td className="border-b p-3">{values.index}</td>
                    <td className="border-b p-3">{values.task}</td>
                    <td className="border-b p-3">
                      <div className="relative pt-1">
                        <div className="overflow-hidden h-2 mb-4 text-xs flex  bg-gray-200">
                          <div
                            style={{ width: `${values.progress}` }}
                            className={`shadow-none flex flex-col text-center whitespace-nowrap text-white justify-center ${
                              values.lable < 56 ? "bg-blue-500" : "bg-red-500"
                            } `}
                          ></div>
                        </div>
                      </div>
                    </td>
                    <td className="border-b p-3">
                      <span
                        className={`inline-flex items-center justify-center px-2 py-1 text-xs font-bold leading-none ${
                          values.lable < 56 ? "bg-blue-500" : "bg-red-500"
                        } text-red-100 rounded-md`}
                      >
                        {values.lable}%
                      </span>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    </>
  );
}
