import React from "react";
import Sdata from "./Sdata";

export default function CondensedTable(props) {
  return (
    <>
      <div className="border-b shadow-lg ml-8 mt-8" style={{ width: "40vw" }}>
        <div className="bg-white">
          <p className="p-2">Condensed Full Width Table</p>
          <hr />
        </div>
        <div className="bg-white mx-4">
          <table className="table-fixed w-96">
            <thead>
              <th className="border-b px-3 pt-1 font-bold w-12">{props.no}</th>
              <th className="border-b px-3 pt-1 font-bold w-72">
                {props.headingone}
              </th>
              <th className="border-b px-3 pt-1 font-bold w-40">
                {props.Kitnahua}
              </th>
              <th className="border-b px-3 pt-1 font-bold w-20">
                {props.badge}
              </th>
            </thead>
            <tbody>
              {Sdata.map((values) => {
                return (
                  <tr key={values.index}>
                    <td className="border-b px-3 pt-1">{values.index}</td>
                    <td className="border-b px-3 pt-1">{values.task}</td>
                    <td className="border-b px-3 pt-1">
                      <div className="relative pt-1">
                        <div className="overflow-hidden h-2 mb-4 text-xs flex  bg-gray-200">
                          <div
                            style={{ width: `${values.progress}` }}
                            className={`shadow-none flex flex-col text-center whitespace-nowrap text-white justify-center ${
                              values.lable < 56 ? "bg-blue-500" : "bg-red-500"
                            } `}
                          ></div>
                        </div>
                      </div>
                    </td>
                    <td className="border-b px-3">
                      <span
                        className={`inline-flex items-center justify-center px-2 py-1 text-xs font-bold leading-none ${
                          values.lable < 56 ? "bg-blue-500" : "bg-red-500"
                        } text-red-100 rounded-md`}
                      >
                        {values.lable}%
                      </span>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    </>
  );
}
