import React from "react";

export default function Navbar() {
  return (
    <div>
      <link
        href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.11.2/css/all.min.css"
        rel="stylesheet"
      />

      {/* <!--this contains my photo, email and some information.--> */}
      <div className="pt-4 w-full ">
        <div className="p-2 text-gray-600 bg-white  border-b-2">
          <span className="mx-12 text-xl text-gray-900 hidden md:flex  float-left ">
            My Website
          </span>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-6 w-6 ml-16 inline"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={1}
              d="M4 6h16M4 12h16M4 18h16"
            />
          </svg>
          <span className="px-5 cursor-pointer hover:text-black">Home</span>

          <span className="px-5 cursor-pointer hover:text-black">Contact</span>
          {/**box icon */}
          <span className="px-5 cursor-pointer float-right hover:text-black">
            <i className="fas fa-th p-2  rounded-full"></i>
          </span>

          {/**notification icon */}
          <span className="px-5 w-8 float-right relative cursor-pointer hover:text-black">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-6 w-6"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={1}
                d="M15 17h5l-1.405-1.405A2.032 2.032 0 0118 14.158V11a6.002 6.002 0 00-4-5.659V5a2 2 0 10-4 0v.341C7.67 6.165 6 8.388 6 11v3.159c0 .538-.214 1.055-.595 1.436L4 17h5m6 0v1a3 3 0 11-6 0v-1m6 0H9"
              />
            </svg>
            <span className="absolute right-0 top-0 -mt-2 -mr-1 text-xs bg-yellow-500 text-white font-medium px-2 shadow-lg rounded-full">
              15
            </span>
          </span>
          {/**chat icon */}
          <span className="w-10 px-5 relative float-right mr-3 cursor-pointer hover:text-gray-700 ">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-6 w-6"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={1}
                d="M17 8h2a2 2 0 012 2v6a2 2 0 01-2 2h-2v4l-4-4H9a1.994 1.994 0 01-1.414-.586m0 0L11 14h4a2 2 0 002-2V6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2v4l.586-.586z"
              />
            </svg>
            <span className="absolute right-0 top-0 -mt-1 -mr-1 text-xs bg-red-500 text-black font-medium px-2 rounded-full">
              4
            </span>
          </span>
          {/**search icon */}
          <span className="px-5 float-right cursor-pointer hover:text-gray-700 hidden sm:flex">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-6 w-6"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={1}
                d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
              />
            </svg>
          </span>
          {/**search bar */}
          <span className="w-screen md:w-1/4 h-10  cursor-pointer border border-gray-300 text-sm rounded-full inline-flex float-right hidden ">
            <input
              type="search"
              name="serch"
              placeholder="Search"
              className="flex-grow px-4 rounded-l-full rounded-r-full text-sm focus:outline-none"
            />
          </span>
        </div>
      </div>
    </div>
  );
}
